/* Home button stylings */

.mybtn1,
.mybtn2 {
  background-color: white;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 50px;
  padding: 12px 36px;
  border: none;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.mybtn2 {
  background-color: #d70d25;
  color: white;
}
.mybtn2:hover {
  color: white;
}
.mybtn1:focus,
.mybtn2:focus {
  box-shadow: none;
}
.mybtn1::before,
.mybtn2::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0%;
  background-color: rgba(0, 0, 0, 0.1);
  height: 100%;
  z-index: -1;
  transition: all 0.3s ease;
}
.mybtn2:hover::before,
.mybtn1:hover::before {
  width: 100%;
}

