/* Shapes */
.home-section .bg-shapes div {
  position: absolute;
  opacity: 0.1;
}
.home-section .bg-shapes div:nth-child(1) {
  height: 100px;
  width: 100px;
  top: 20px;
  left: 20%;
  background: whitesmoke;
  border-radius: 50%;
  -webkit-animation: shapeani01 5s linear infinite;
          animation: shapeani01 5s linear infinite;
}

.home-section .bg-shapes div:nth-child(2) {
  height: 100px;
  width: 100px;
  top: 40%;
  left: calc(0% - 50px);
  background: transparent;
  border: 5px solid white;
  -webkit-animation: shapeani02 10s linear infinite;
          animation: shapeani02 10s linear infinite;
}

.home-section .bg-shapes div:nth-child(3) {
  height: 200px;
  width: 200px;
  top: 40%;
  left: calc(100% - 100px);
  background-color: white;
  border-radius: 50%;
  opacity: 0.08;
  -webkit-animation: shapeani03 7s linear infinite;
          animation: shapeani03 7s linear infinite;
}

/* Shape anaimations */
@-webkit-keyframes shapeani01 {
  0%,
  100% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(50px);
  }
}
@keyframes shapeani01 {
  0%,
  100% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(50px);
  }
}
@-webkit-keyframes shapeani02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes shapeani02 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes shapeani03 {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes shapeani03 {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}

/* Image responsive */
@media only screen and (max-width: 600px) {
  .home-section .home-img img {
    max-width: 400px;
    width: 100%;
    position: relative;
    left: 0;
    top: 20px;
    -webkit-animation: productani01 5s linear infinite;
            animation: productani01 5s linear infinite;
    align-items: center;
    justify-content: center;
  }
}

